.searchWrapper {
  position: relative;
  min-height: 80%;
  min-width: 15%;

  .searchIcon{
    position: absolute;
    top: 50%;
    left:50%;
    width:32px;
    height: 32px;
    transform: translate(-50%,-50%);
  }
}

@media  (min-width:950px) {
  .searchWrapper{
    display: none;
  }
}